* {
  padding: 0;
  margin: 0
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul.ant-menu {
  border-right: none
}


.edit-btn{
  color: #1890ff;
  margin-right:10px;
  cursor: pointer
}
.delete-btn{
  color: #f00;
  cursor: pointer
}

.menu-badge .ant-badge-count{
  font-size: 12px;
  width: 20px;
  height: 20px;
  line-height: 20px;
  margin-top:-20px;
  margin-left:-6px;
  padding: 0;
  background: #e54155;
  box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.12);
}

.flex-wrap{
  display: flex;
  align-items: center;
}

.padding-left-30{
  padding-left:30px;
}
.header {
  display: flex;
  justify-content: space-between; }
  .header .logo {
    display: inline-block;
    color: #fff;
    font-size: 20px; }
  .header .user {
    display: flex;
    justify-content: flex-end; }
    .header .user .add-patient {
      height: 64px;
      display: flex;
      cursor: pointer;
      width: 130px;
      align-items: center;
      justify-content: center; }
      .header .user .add-patient .icon {
        font-size: 30px;
        color: #fff;
        vertical-align: middle; }
      .header .user .add-patient span {
        color: #fff;
        margin-left: 5px; }
    .header .user .user-info {
      min-width: 130px;
      height: 64px;
      cursor: pointer;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center; }
      .header .user .user-info img {
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 20px;
        border: 0; }
      .header .user .user-info span {
        color: #fff;
        margin-left: 10px; }
      .header .user .user-info .user-item-wrap {
        position: absolute;
        width: 84px;
        z-index: 9;
        background: #fff;
        border: 1px solid #eee;
        box-sizing: border-box;
        left: -10px; }
        .header .user .user-info .user-item-wrap .user-item {
          height: 40px;
          line-height: 40px; }
          .header .user .user-info .user-item-wrap .user-item:hover {
            color: #1890ff; }
    .header .user .logout {
      width: 64px;
      height: 64px;
      text-align: center;
      float: left;
      cursor: pointer;
      position: relative; }
      .header .user .logout .icon {
        font-size: 30px;
        color: #fff;
        vertical-align: middle; }

.bold {
  font-weight: bold; }

.login-wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: -webkit-linear-gradient(left, #00affc, #0098ff);
  background: linear-gradient(to right, #00affc, #0098ff);
  display: flex;
  justify-content: center;
  align-items: center; }
  .login-wrap .login-center {
    display: inline-block;
    width: 500px;
    padding-bottom: 30px;
    background: #fff;
    position: absolute;
    border-radius: 5px;
    overflow: hidden; }
    .login-wrap .login-center .login-title {
      height: 80px;
      line-height: 80px;
      text-align: center;
      font-size: 20px; }
    .login-wrap .login-center .login-form {
      max-width: 300px;
      margin: 0 auto; }
      .login-wrap .login-center .login-form .forget-password {
        text-align: center;
        color: #1890ff;
        text-decoration: underline;
        cursor: pointer; }
    .login-wrap .login-center .login-form-forgot {
      float: right; }
    .login-wrap .login-center .login-form-button {
      width: 100%; }
    .login-wrap .login-center .ant-form-item {
      margin-bottom: 10px;
      height: 40px; }
    .login-wrap .login-center p.err-msg {
      color: #f00;
      text-align: center;
      height: 20px;
      margin-bottom: 10px; }
    .login-wrap .login-center .bottom-btn {
      text-align: center;
      text-decoration: underline;
      color: #1890ff;
      cursor: pointer; }
      .login-wrap .login-center .bottom-btn span {
        margin: 0 10px; }
    .login-wrap .login-center .change-pass-success {
      padding-top: 50px; }
      .login-wrap .login-center .change-pass-success .success-icon {
        text-align: center;
        font-size: 50px;
        color: #0098ff; }
      .login-wrap .login-center .change-pass-success .success-words {
        text-align: center;
        padding-bottom: 20px; }

._form-wrap .btn-wrap button {
  margin-right: 10px; }

._form-wrap.edit-form-wrap {
  display: flex; }
  ._form-wrap.edit-form-wrap form {
    width: 70%;
    margin-right: 20px;
    border-right: 1px solid #ccc;
    padding-right: 60px; }

._form-wrap .codeImg {
  margin: 10px 0 0 10px; }

.search-input {
  width: 200px; }

.ant-pagination {
  text-align: right;
  margin: 10px 0; }

.opt-bar {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.td-img {
  width: 30px;
  height: 30px;
  display: block;
  cursor: pointer; }

.opt-group button {
  padding: 10px;
  display: block;
  background: none !important;
  border: none !important; }

.my_modal {
  padding: 0; }
  .my_modal .ant-modal-content {
    height: 100%;
    overflow: hidden; }
  .my_modal .ant-modal-body {
    height: calc(100% - 55px);
    overflow: auto; }
  .my_modal .close-btn {
    cursor: pointer; }
  .my_modal .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center; }
    .my_modal .top p {
      margin: 0; }
  .my_modal .status-dropdown {
    margin-right: 20px; }

.red-item, .blue-item, .gray-item {
  display: flex;
  align-items: center; }
  .red-item::before, .blue-item::before, .gray-item::before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 10px;
    margin-right: 5px; }

.red-item::before {
  background: red; }

.blue-item::before {
  background: blue; }

.gray-item::before {
  background: gray; }

.secret-table th, .secret-table td {
  padding: 4px 4px 4px 8px !important; }

.secret-table td {
  cursor: pointer; }

.secret-table .no-wrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; }

.secret-table .opt-td {
  padding: 0 10px; }
  .secret-table .opt-td i {
    margin-right: 10px;
    font-size: 16px; }

.secret-table .ant-table-tbody .red-row {
  background: rgba(255, 0, 0, 0.5); }

.secret-table .ant-table-tbody .blue-row {
  background: rgba(0, 0, 255, 0.5); }

.secret-table .audio_url {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.flex-wrap {
  display: flex; }

.btn-wrap button {
  margin-right: 20px; }

.user-control-wrap .top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px; }

